// Imports
import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../../node_modules/laravel-mix/node_modules/css-loader/dist/runtime/cssWithMappingToString.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/laravel-mix/node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".middot{color:#d6d6d6}", "",{"version":3,"sources":["webpack://./resources/js/components/tables/TaxaTable.vue"],"names":[],"mappings":"AAwOA,QACA,aACA","sourcesContent":["<template>\r\n  <div class=\"taxa-table\">\r\n    <div class=\"level\">\r\n      <div class=\"level-left\">\r\n        <div class=\"level-item\">\r\n          <button\r\n              type=\"button\"\r\n              class=\"button is-touch-full\"\r\n              @click=\"showFilter = !showFilter\"\r\n          >\r\n            <b-icon icon=\"filter\" :class=\"[filterIsActive ? 'has-text-primary' : 'has-text-grey-light']\" />\r\n            <span>{{ trans('buttons.filters') }}</span>\r\n          </button>\r\n        </div>\r\n      </div>\r\n\r\n      <div class=\"level-right\" v-if=\"hasActions\">\r\n        <div class=\"level-item\">\r\n          <b-dropdown position=\"is-bottom-left\">\r\n            <button\r\n              class=\"button is-touch-full\"\r\n              slot=\"trigger\"\r\n            >\r\n              <span>{{ trans('labels.actions') }}</span>\r\n\r\n              <span class=\"icon has-text-grey\">\r\n                <i class=\"fa fa-angle-down\"></i>\r\n              </span>\r\n            </button>\r\n\r\n            <b-dropdown-item\r\n              @click=\"openExportModal\"\r\n              v-if=\"exportable\"\r\n            >\r\n              <b-icon icon=\"download\" class=\"has-text-grey\" />\r\n\r\n              <span>{{ trans('buttons.export') }}</span>\r\n            </b-dropdown-item>\r\n\r\n            <b-dropdown-item\r\n              @click=\"openAdminExportModal\"\r\n              v-if=\"adminExportable\"\r\n            >\r\n              <b-icon icon=\"download\" class=\"has-text-grey\" />\r\n\r\n              <span>{{ trans('buttons.admin_export') }}</span>\r\n            </b-dropdown-item>\r\n          </b-dropdown>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <b-collapse :open=\"showFilter\" class=\"mt-4\">\r\n      <form @submit.prevent=\"applyFilter\">\r\n        <div class=\"columns\">\r\n          <b-field :label=\"trans('labels.taxa.rank')\" class=\"column is-half\">\r\n            <b-select v-model=\"newFilter.rank\" expanded>\r\n              <option value=\"\"></option>\r\n              <option\r\n                v-for=\"(rank, index) in ranks\"\r\n                :value=\"rank.value\"\r\n                :key=\"index\"\r\n                v-text=\"rank.label\"\r\n              />\r\n            </b-select>\r\n          </b-field>\r\n\r\n          <div class=\"column is half\">\r\n            <nz-taxon-autocomplete\r\n              @select=\"onTaxonSelect\"\r\n              v-model=\"newFilter.name\"\r\n              :taxon=\"newFilter.selectedTaxon\"\r\n              :label=\"trans('labels.taxa.name')\"\r\n              placeholder=\"\"\r\n            />\r\n            <b-checkbox v-model=\"newFilter.includeChildTaxa\">{{ trans('labels.taxa.include_lower_taxa') }}</b-checkbox>\r\n          </div>\r\n        </div>\r\n\r\n        <button type=\"submit\" class=\"button is-primary is-outlined\" @click=\"applyFilter\">{{ trans('buttons.apply') }}</button>\r\n\r\n        <button type=\"button\" class=\"button\" @click=\"clearFilter\">{{ trans('buttons.clear') }}</button>\r\n      </form>\r\n    </b-collapse>\r\n\r\n    <div v-if=\"taxonomy\">\r\n      <hr>\r\n      <b-message type=\"is-success\" class=\"has-text-centered\">\r\n        This taxonomic database is connected to <a v-bind:href=\"taxonomyLink\" target=\"_blank\">{{ taxonomyLink }}</a>\r\n      </b-message>\r\n    </div>\r\n\r\n    <hr>\r\n\r\n    <b-table\r\n      :data=\"data\"\r\n      :loading=\"loading\"\r\n\r\n      paginated\r\n      backend-pagination\r\n      :total=\"total\"\r\n      :per-page=\"perPage\"\r\n      :current-page=\"page\"\r\n      @page-change=\"onPageChange\"\r\n      pagination-position=\"both\"\r\n\r\n      backend-sorting\r\n      default-sort-direction=\"asc\"\r\n      :default-sort=\"[sortField, sortOrder]\"\r\n      @sort=\"onSort\"\r\n\r\n      mobile-cards\r\n\r\n      :checkable=\"hasActions\"\r\n      :checked-rows.sync=\"checkedRows\"\r\n    >\r\n      <template #top-left>\r\n        <div class=\"level-item\">\r\n          <nz-per-page-select :value=\"perPage\" @input=\"onPerPageChange\" :options=\"perPageOptions\" />\r\n        </div>\r\n        <div class=\"level-item\">{{ showing }}</div>\r\n      </template>\r\n      <template #bottom-left>\r\n        <div class=\"level-item\">\r\n          <nz-per-page-select :value=\"perPage\" @input=\"onPerPageChange\" :options=\"perPageOptions\" />\r\n        </div>\r\n        <div class=\"level-item\">{{ showing }}</div>\r\n      </template>\r\n\r\n      <b-table-column field=\"id\" :label=\"trans('labels.id')\" width=\"40\" numeric sortable>\r\n        <template #default=\"{ row }\">\r\n          {{ row.id }}\r\n        </template>\r\n        <template #header=\"{ column }\">\r\n          <nz-sortable-column-header :column=\"column\" :sort=\"{ field: sortField, order: sortOrder }\" />\r\n        </template>\r\n      </b-table-column>\r\n\r\n      <b-table-column field=\"rank_level\" :label=\"trans('labels.taxa.rank')\" sortable>\r\n        <template #default=\"{ row }\">\r\n          {{ row.rank_translation }}\r\n        </template>\r\n        <template #header=\"{ column }\">\r\n          <nz-sortable-column-header :column=\"column\" :sort=\"{ field: sortField, order: sortOrder }\" />\r\n        </template>\r\n      </b-table-column>\r\n\r\n      <b-table-column field=\"name\" :label=\"trans('labels.taxa.name')\" sortable>\r\n        <template #default=\"{ row }\">\r\n          <span v-html=\"changeSpaceIntoDot(row.name) + (row.native_name ? ` (${changeSpaceIntoDot(row.native_name)})` : '')\"></span>\r\n          <b-tooltip :label=\"getSynonyms(row.synonyms)\" multilined dashed v-if=\"row.synonyms.length > 0\">\r\n            <b-icon icon=\"comment\"></b-icon>\r\n          </b-tooltip>\r\n        </template>\r\n        <template #header=\"{ column }\">\r\n          <nz-sortable-column-header :column=\"column\" :sort=\"{ field: sortField, order: sortOrder }\" />\r\n        </template>\r\n      </b-table-column>\r\n\r\n      <b-table-column field=\"author\" :label=\"trans('labels.taxa.author')\" sortable>\r\n        <template #default=\"{ row }\">\r\n          {{ row.author }}\r\n        </template>\r\n        <template #header=\"{ column }\">\r\n          <nz-sortable-column-header :column=\"column\" :sort=\"{ field: sortField, order: sortOrder }\" />\r\n        </template>\r\n      </b-table-column>\r\n\r\n      <b-table-column width=\"150\" numeric v-slot=\"{ row }\" v-if=\"! taxonomy\">\r\n        <a @click=\"openActivityLogModal(row)\" v-if=\"showActivityLog && row.activity && row.activity.length > 0\" :title=\"trans('Activity Log')\"><b-icon icon=\"history\" /></a>\r\n\r\n        <a :href=\"editLink(row)\" v-if=\"row.can_edit\"><b-icon icon=\"edit\"></b-icon></a>\r\n\r\n        <a @click=\"confirmRemove(row)\" v-if=\"row.can_delete\"><b-icon icon=\"trash\"></b-icon></a>\r\n      </b-table-column>\r\n\r\n      <b-table-column width=\"150\" numeric v-slot=\"{ row }\" v-if=\"taxonomy\">\r\n        <a :href=\"editLink(row)\" v-if=\"row.can_edit\"><b-icon icon=\"eye\"></b-icon></a>\r\n\r\n        <a @click=\"confirmRemove(row)\" v-if=\"row.can_delete && ! row.taxonomy_id\"><b-icon icon=\"trash\"></b-icon></a>\r\n      </b-table-column>\r\n\r\n      <template #empty>\r\n        <section class=\"section\">\r\n          <div class=\"content has-text-grey has-text-centered\">\r\n            <p>{{ empty }}</p>\r\n          </div>\r\n        </section>\r\n      </template>\r\n    </b-table>\r\n\r\n    <b-modal :active=\"activityLog.length > 0\" @close=\"activityLog = []\" has-modal-card>\r\n      <div class=\"modal-card\">\r\n        <div class=\"modal-card-head\">\r\n          <b-icon icon=\"history\" />\r\n          <p class=\"modal-card-title\">{{ trans('Activity Log') }}</p>\r\n        </div>\r\n        <div class=\"modal-card-body\">\r\n          <nz-taxon-activity-log :activities=\"activityLog\" />\r\n        </div>\r\n      </div>\r\n    </b-modal>\r\n\r\n    <b-modal :active=\"showExportModal\" @close=\"showExportModal = false\" has-modal-card :can-cancel=\"[]\">\r\n      <nz-export-modal\r\n        :checked=\"checkedIds\"\r\n        :filter=\"filter\"\r\n        :columns=\"exportColumns\"\r\n        :url=\"exportUrl\"\r\n        :types=\"['custom']\"\r\n        :sort=\"sortBy\"\r\n        @cancel=\"showExportModal = false\"\r\n        @done=\"onExportDone\"\r\n      />\r\n    </b-modal>\r\n\r\n    <b-modal :active=\"showAdminExportModal\" @close=\"showAdminExportModal = false\" has-modal-card :can-cancel=\"[]\">\r\n      <nz-export-modal\r\n        :checked=\"checkedIds\"\r\n        :filter=\"filter\"\r\n        :columns=\"adminExportColumns\"\r\n        :url=\"adminExportUrl\"\r\n        :types=\"['custom']\"\r\n        :sort=\"sortBy\"\r\n        @cancel=\"showAdminExportModal = false\"\r\n        @done=\"onAdminExportDone\"\r\n      />\r\n    </b-modal>\r\n  </div>\r\n</template>\r\n\r\n<style>\r\n.middot {\r\n  color: #d6d6d6;\r\n}\r\n</style>\r\n\r\n<script>\r\nimport FilterableTableMixin from '@/mixins/FilterableTableMixin'\r\nimport PersistentTableMixin from '@/mixins/PersistentTableMixin'\r\nimport ExportDownloadModal from '@/components/exports/ExportDownloadModal'\r\nimport NzTaxonAutocomplete from '@/components/inputs/TaxonAutocomplete'\r\nimport NzPerPageSelect from '@/components/table/PerPageSelect'\r\nimport NzSortableColumnHeader from '@/components/table/SortableColumnHeader'\r\nimport NzExportModal from '@/components/exports/ExportModal'\r\n\r\nexport default {\r\n  name: 'nzTaxaTable',\r\n\r\n  mixins: [FilterableTableMixin, PersistentTableMixin],\r\n\r\n  components: {\r\n    NzTaxonAutocomplete,\r\n    NzPerPageSelect,\r\n    NzSortableColumnHeader,\r\n    NzExportModal\r\n  },\r\n\r\n  props: {\r\n    perPageOptions: {\r\n      type: Array,\r\n      default: () => [15, 30, 50, 100],\r\n      validator: value => value.length\r\n    },\r\n    listRoute: String,\r\n    editRoute: String,\r\n    deleteRoute: String,\r\n    empty: {\r\n      type: String,\r\n      default: 'Nothing here.'\r\n    },\r\n    ranks: Array,\r\n    showActivityLog: Boolean,\r\n    exportColumns: Array,\r\n    adminExportColumns: Array,\r\n    exportUrl: String,\r\n    adminExportUrl: String,\r\n    taxonomy: Boolean,\r\n    taxonomyLink: String,\r\n  },\r\n\r\n  data() {\r\n    return {\r\n      data: [],\r\n      total: 0,\r\n      loading: false,\r\n      sortField: 'id',\r\n      sortOrder: 'desc',\r\n      page: 1,\r\n      perPage: this.perPageOptions[0],\r\n      checkedRows: [],\r\n      activityLog: [],\r\n      showExportModal: false,\r\n      showAdminExportModal: false\r\n    }\r\n  },\r\n\r\n  computed: {\r\n    hasActions() {\r\n      return this.exportable\r\n    },\r\n\r\n    exportable() {\r\n      return !!(this.exportUrl && this.exportColumns.length)\r\n    },\r\n\r\n    adminExportable() {\r\n      return !!(this.adminExportUrl && this.adminExportColumns.length)\r\n    },\r\n\r\n    checkedIds() {\r\n      return this.checkedRows.map(row => row.id)\r\n    },\r\n\r\n    sortBy() {\r\n      return `${this.sortField}.${this.sortOrder}`\r\n    },\r\n\r\n    showing() {\r\n      const to = this.page * this.perPage <= this.total\r\n        ? this.page * this.perPage\r\n        : this.total\r\n\r\n      const from = this.page > 1 ? (this.page - 1) * this.perPage + 1 : 1\r\n\r\n      return this.total ? this.trans('labels.tables.from_to_total', {\r\n          total: this.total,\r\n          from,\r\n          to\r\n      }) : '';\r\n    },\r\n  },\r\n\r\n  created() {\r\n    this.restoreState()\r\n    this.addMissingValuesAfterRestoration()\r\n    this.loadAsyncData()\r\n\r\n    this.$on('filter', () => {\r\n      this.page = 1\r\n      this.saveState()\r\n      this.loadAsyncData()\r\n    })\r\n  },\r\n\r\n  methods: {\r\n    loadAsyncData() {\r\n      this.loading = true\r\n      this.checkedRows = []\r\n\r\n      const { selectedTaxon, ...filter } = this.filter\r\n\r\n      return axios.get(route(this.listRoute).withQuery({\r\n        ...filter,\r\n        sort_by: this.sortBy,\r\n        page: this.page,\r\n        per_page:this.perPage,\r\n      })).then(({ data: response }) => {\r\n        this.data = response.data\r\n        this.total = response.meta.total\r\n        this.loading = false\r\n      }, (response) => {\r\n        this.data = []\r\n        this.total = 0\r\n        this.loading = false\r\n      })\r\n    },\r\n\r\n    getSynonyms(synonyms) {\r\n      return synonyms.map((synonym) => synonym.name).join(\", \");\r\n    },\r\n\r\n    /*\r\n     * Handle page-change event\r\n     */\r\n    onPageChange(page) {\r\n      this.page = page\r\n\r\n      this.saveState()\r\n\r\n      this.loadAsyncData()\r\n    },\r\n\r\n    /*\r\n     * Handle sort event\r\n     */\r\n    onSort(field, order) {\r\n      this.sortField = field\r\n      this.sortOrder = order\r\n\r\n      this.saveState()\r\n\r\n      this.loadAsyncData()\r\n    },\r\n\r\n    onPerPageChange(perPage) {\r\n      if (perPage === this.perPage) return\r\n\r\n      this.perPage = perPage\r\n\r\n      this.saveState()\r\n\r\n      this.loadAsyncData()\r\n    },\r\n\r\n    confirmRemove(row) {\r\n      this.$buefy.dialog.confirm({\r\n        message: this.trans('Are you sure you want to delete this record?'),\r\n        confirmText: this.trans('buttons.delete'),\r\n        cancelText: this.trans('buttons.cancel'),\r\n        type: 'is-danger',\r\n        onConfirm: () => { this.remove(row) }\r\n      })\r\n    },\r\n\r\n    remove (row) {\r\n      return axios.delete(route(this.deleteRoute, row.id)).then(response => {\r\n        this.$buefy.toast.open({\r\n          message: this.trans('Record deleted'),\r\n          type: 'is-success'\r\n        })\r\n\r\n        this.loadAsyncData()\r\n      }).catch(error => { console.error(error) })\r\n    },\r\n\r\n    editLink (row) {\r\n      return route(this.editRoute, row.id)\r\n    },\r\n\r\n    openActivityLogModal(row) {\r\n      this.activityLog = row.activity\r\n    },\r\n\r\n    filterDefaults() {\r\n      return {\r\n        name: null,\r\n        rank: null,\r\n        taxonId: null,\r\n        includeChildTaxa: null,\r\n        selectedTaxon: null,\r\n        id: null,\r\n      }\r\n    },\r\n\r\n    openExportModal() {\r\n      this.showExportModal = true\r\n    },\r\n\r\n    openAdminExportModal() {\r\n      this.showAdminExportModal = true\r\n    },\r\n\r\n    onExportDone(finishedExport) {\r\n      this.showExportModal = false\r\n\r\n      if (finishedExport.url) {\r\n        this.$buefy.modal.open({\r\n          parent: this,\r\n          component: ExportDownloadModal,\r\n          canCancel: [],\r\n          hasModalCard: true,\r\n          props: {\r\n            url: finishedExport.url,\r\n          }\r\n        })\r\n      } else {\r\n        this.$buefy.toast.open({\r\n          duration: 0,\r\n          message: `Something's not good, also I'm on bottom`,\r\n          type: 'is-danger'\r\n        })\r\n      }\r\n    },\r\n\r\n    onAdminExportDone(finishedExport) {\r\n      this.showAdminExportModal = false\r\n\r\n      if (finishedExport.url) {\r\n        this.$buefy.modal.open({\r\n          parent: this,\r\n          component: ExportDownloadModal,\r\n          canCancel: [],\r\n          hasModalCard: true,\r\n          props: {\r\n            url: finishedExport.url,\r\n          }\r\n        })\r\n      } else {\r\n        this.$buefy.toast.open({\r\n          duration: 0,\r\n          message: `Something's not good, also I'm on bottom`,\r\n          type: 'is-danger'\r\n        })\r\n      }\r\n    },\r\n\r\n    onTaxonSelect(taxon) {\r\n      this.newFilter.taxonId = taxon ? taxon.id : null\r\n      this.newFilter.selectedTaxon = taxon\r\n    },\r\n\r\n    getPersistantKeys() {\r\n      return [\r\n        'sortField', 'sortOrder', 'perPage', 'page',\r\n        'newFilter', 'filter', 'filterIsActive'\r\n      ]\r\n    },\r\n\r\n    addMissingValuesAfterRestoration() {\r\n      this.page = this.page || 1\r\n      this.perPage = this.perPage || this.perPageOptions[0]\r\n      this.sortField = this.sortField || 'id'\r\n      this.sortOrder = this.sortOrder || 'desc'\r\n    },\r\n\r\n    changeSpaceIntoDot(text) {\r\n      return text.replace(/\\s/g, '<span class=\"middot\">&middot;</span>');\r\n    }\r\n  },\r\n}\r\n</script>\r\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
